<template>
    <div class="wrap">
      <van-nav-bar
        title="申请信息"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="mainCotent">
        <div class="checking">
          <img src="../../../assets/images/mall/checking.png" alt="">
        </div>
        <p class="p1">审核中</p>
        <p class="p2">认证信息已提交，请等待审核</p>
        <div class="makeSure" @click="goTo">
          <img src="../../../assets/images/mall/sure.png" alt="">
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "inReview",
      data () {
          return {

          }
      },
      methods: {
        onClickLeft(){
          this.$router.back();
        },
        goTo () {
          this.$router.push('/mall/gtja/applyRecord')
        }
      }
    }
</script>

<style scoped>
  .wrap {
    background:rgba(247,248,250,1);
    min-height: 100vh;
  }
.mainCotent {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  align-items: center;
  background: #fff;
  min-height: calc(100vh - 56px);
}
  .checking {
    width: 60px;
    height: 60px;
    margin: 94px 0 16px;
  }
  .checking img {
    width: 100%;
    height: 100%;
  }
  .p1 {
    font-size:25px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(32,32,32,1);
    line-height:35px;
  }
  .p2 {
    margin: 4px 0 88px;
    font-size:14px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(32,32,32,1);
    line-height:20px;
  }
  .makeSure {
    width: 161px;
    height: 62px;
  }
  .makeSure img {
    width: 100%;
    height: 100%;
  }
</style>



